a:active,
a:visited {
  text-decoration: none;
  color: inherit;
}

.App_titleContainer__1lwLv {
  background-color: white;
  width: 100%;
  text-align: center;
  margin-top: 10vh;
  margin: 25px;
}

.App_hoverEffect__3Yzzw {
  position: relative;
  color: #000;
  /* text-decoration: none; */
}

.App_hoverEffect__3Yzzw:hover {
  color: #000;
}

.App_hoverEffect__3Yzzw::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 15px;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.52);
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.App_hoverEffect__3Yzzw:hover::before {
  visibility: visible;
  transform: scaleX(1);
}

